<template>
	<div class="bannerAdminArea" :class="['bannerAdmin-wrap', 'swiper-container-horizontal', 'swiper-container-android']" style="overflow:hidden">
		<ul class="swiper-wrapper">
			<li class="swiper-slide" v-for="(bannerAdmin,index) in getMainBannerAdminList" :key="index">

				<template v-if="extractUrl(bannerAdmin.bannerAdminUrl) !== null && extractUrl(bannerAdmin.bannerAdminUrl) !== undefined && extractUrl(bannerAdmin.bannerAdminUrl) !== '' ">
						<a >
							<youtube class="bannerAdmin_video" :video-id="extractUrl(bannerAdmin.bannerAdminUrl)" ref="youtube" @playing="playing(0)" @paused="paused(0)"></youtube>
						</a>
					</template>

					<template v-else-if="bannerAdmin.bannerAdminFile[0].fileType==='MOVIE'">

						<video  class="myVideo" width="100%" height="251px" controls>
							<source v-if="bannerAdmin.bannerAdminFile[0].fileExtension == '.mp4'" :src="bannerAdmin.bannerAdminFile[0].filePath" type='video/mp4;'>
							<source v-if="bannerAdmin.bannerAdminFile[0].fileExtension == '.ogv'" :src="bannerAdmin.bannerAdminFile[0].filePath" type='video/ogg;'>
						</video>
					</template>


					<template v-else>
						<a :href="bannerAdmin.bannerAdminUrl">
							<img class="bannerAdmin-image" :src="bannerAdmin.bannerAdminFile[0].filePath">
						</a>
					</template>

			</li>
		</ul>
	</div>
</template>

<script>
import Swiper, { SwiperOptions } from "swiper";
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
let swiper = null
export default {
  mixins: [myMixin],
	components: {
		Swiper,
	},
	data() {
		return {
			
			video1Flag: false,
			autoplay:3000
		}
	},
	computed: {
		...mapGetters(['getMainBannerAdminList'])
	},
	methods: {
		
		playing(index){
			
			console.log('play')
			swiper.autoplay.stop();
		},
		paused(index){

			swiper.autoplay.start();
		},
	},
	updated() {
		swiper = new Swiper('.bannerAdminArea', {
			mode: 'horizontal',
			preventLinks: true,
			resistance: '100%',
			slidesPerView: 1,
			spaceBetween: 20,
			autoplay: {
				delay: this.autoplay,
				disableOnInteraction: false,
			},
			pagination: {
				el: '.swiper-pagination',
				clickable: true,
			}
		});

		
		document.querySelectorAll('.myVideo').forEach(function (item, idx) {
			item.addEventListener("play", function(){

				swiper.autoplay.stop();
			});
			

			item.addEventListener("pause", function(){

				swiper.autoplay.start();
			});
		});

		
	}
};
</script>